*{
  font-family:IBM Plex Mono;
}
body{
  width: 100vw;
  height: 100vh;
}
#theme-icon{
  color: yellow;
  font-size: 20px;
 
}
#theme-light{
  color:#888;
  font-size: 20px;
 
}
.App{
  background-color: #121120;
  color:white;
  width: 100%;
  height: 100%;
}
.App-light{
  color:black;
  background-color: rgb(220, 210, 210);
}
.body-light{
  background-color: #dddbf1;
}
.home-cont{
  padding: 2em;
 
}
.home-cont nav{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
.home-container{
  padding: 0 2em;
  margin: 0 2em 2em 0;
  height: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap:wrap;
  
}
/*Left Container of home*/
.right-container{
  width: 300px;
  padding: 2em;
  margin-left: 3em;
  background-color: #26273C;
  border-radius: 10px;
  text-align: center;
}
.right-light{
  width: 300px;
  padding: 2em;
  margin-left: 3em;
  border-radius: 10px;
  text-align: center;
  background-color: #f8f8ff;
}
.right-container:hover, .right-light:hover{
  box-shadow: 5px 10px #FB7171;
}
.label{
  margin-top: 4em;
}
.Kumusta{
  font-size: clamp(18px, 1.5vw, 36px);
 
}
.name{
  font-size: clamp(18px, 2vw, 38px);
  
}
.name-light{
  
}
/*new*/
.web-dev{
  font-size: clamp(18px, 2vw, 38px);
  color: #FB7171;
}
.contact{
  background-color: #9900F8;
  padding: 1em ;
  width: 270px;
  border-radius: 10px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  color:white;
}
.contact:hover{
 filter:brightness(60%);
}
/*Right image*/
@keyframes expand{
  0% {width: 280px;}
  25% {width: 290px;}
  50%{transform: rotate(20deg);  }
  70%{transform: rotate(-20deg);}
  100%{width: 300px;}
}
@keyframes fade-in {
  0%{opacity: 100%;}
  10%{opacity: 20%;}
  40%{opacity: 40%;}
  60%{opacity: 60%;}
  80%{opacity: 80%;}
  100%{opacity: 100%;}
}
.Nicole{
  width: 20em;
  border-radius: 2em;
  animation: fade-in 1s;
}
.Nicole:hover{
  animation-name:  expand;
  animation-duration: 2s ;
}


/*About*/
.About-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap:wrap;
  padding-top: 2em;
}
.about-me, .skills{
  width: 300px;
  height: 70%;
  justify-content: space-around;
  background-color:#26273C ;
  color:white;
  padding: 1em;
  border-radius: 10px;
  margin: 1em;
}
.about-light, .skills-light{
  width: 300px;
  height: 70%;
  justify-content: space-around;
  background-color:white ;
  color:black;
  padding: 1em;
  border-radius: 10px;
  margin: 1em;
}

.about-me:hover, .about-light:hover{
  box-shadow: 5px 10px #F0F413;
}
.skills:hover , .skills-light:hover{
  box-shadow: 5px 10px #FB7171;
}
#fa-envelope, #fa-download{
  color: #F0F413;
}
.email_add, #ClickMe{
  color: #FB7171;
}
.quote{
  font-weight: 700;
}


/*Projects*/
.my-projects-title{
  margin-top: 4em;
  font-size: 25px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* width */
.Projects-container::-webkit-scrollbar {
  width: 2px;
  height: 5px;
}

/* Track */
.Projects-container::-webkit-scrollbar-track {
  background:#121120;
}

/* Handle */
.Projects-container::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
.Projects-container::-webkit-scrollbar-thumb:hover {
  background: rgb(181, 39, 39);
}
.Projects-container{
  display: flex;
  flex-direction: row;
  overflow-x:scroll;
}
.node{
  margin: 0 5px;
  width: 290px;
  background-color: #26273C;
  border-radius: 10px;
  padding: 1em;
  transform: scale(0.8);
}
.node-light{
  margin: 0 5px;
  width: 290px;
  background-color: #ffffff;
  color: black;
  border-radius: 10px;
  padding: 1em;
  transform: scale(0.8);
}
.node:hover, .node-light:hover{
  transform: scale(1);
}
.proj-img {
  width: 280px;
}

.proj-link{
  text-decoration: none;
  background-color: #9900F8;
  color:white;
  padding: 1em;
  border-radius: 10px;
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.proj-link:hover{
  filter: brightness(60%);
}
/*Footer*/
.Footer-container{
  width: 100%;
  height: 290px;
  background-color: rgba(255, 255, 255);
  padding-bottom: 1em;
  text-align: center;
  margin-top: 2em 
}
.Footer-container div{
  display:flex;
  flex-direction: column;
  align-items: center;
}

.icons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-size: 28px;
  width: 300px;
  list-style: none;
}

#iconf{
  color:black;
}

#iconf:hover{
  color:#9900F8;
}
